.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.header{
   text-align: center;
   border: 2px solid black;
   background-color: lightsteelblue;

}
.container{
  display: flex;
  flex-direction:row;
  border: 2px solid black;
  margin: 3px;
  
}

.box1,.box2{
  margin:25px;
  position:relative;
  border: 2px solid black;
  text-align: center;
  height:400px;
  width:700px;

}
.footer{
  text-align: center;
   border: 2px solid black;
   background-color: lightsteelblue;;

}